import { useState, createRef, useMemo, useRef, FC } from 'react';
import classNames from 'classnames';
import ReactDatePicker, { ReactDatePickerProps } from 'react-datepicker';

// Components
import Button from '../Button';
import Input, { InputProps } from '../Input';

// Hooks
import { useClickEscape } from '@dtci/triton/hooks';

// Styles
import './DateInput.scss';

// Types
import { TestMetadata } from 'types';

export interface DateInputProps extends Partial<ReactDatePickerProps> {
  /** Custom class name(s) for component container */
  containerClassName?: string;
  /** If label is not visible, use aria-label for screen readers */
  labelText?: string;
  /** Determines if you want the calendar to close when escape is pressed */
  closeOnEscape?: boolean;
  /** control selection event */
  onDateChange?: (date?: Date) => void;
  /** Additional input props for Input, based on the Input Component */
  inputProps?: InputProps;
  /** External value for the component */
  selected?: Date;
  /** Error State */
  isError?: boolean;
  /** Sets test id */
  testMetadata?: {
    button?: TestMetadata;
    input?: TestMetadata;
    wrapper?: TestMetadata;
  };
  /** Additional react-datepicker props */
  reactDatePickerProps?: Partial<ReactDatePickerProps>;
}

const DateInput: FC<DateInputProps> = ({
  containerClassName,
  labelText,
  selected,
  closeOnEscape,
  inputProps,
  onDateChange,
  showPopperArrow,
  isError = false,
  testMetadata,
  reactDatePickerProps,
  ...otherProps
}) => {
  const [open, setOpen] = useState(false);
  const inputRef = createRef<ReactDatePicker>();

  const dateInputClassNames = useMemo(
    () =>
      classNames(
        'sb-date-input',
        {
          'sb-date-input_error': isError,
          'sb-date-input_disabled': otherProps.disabled,
          'sb-date-input__control--is-focused': open,
        },
        containerClassName,
      ),
    [containerClassName, otherProps.disabled, open, isError],
  );

  useClickEscape(() => {
    if (closeOnEscape && inputRef) {
      inputRef.current?.setOpen(false);
    }
  }, [inputRef]);

  const customInputRef: React.LegacyRef<HTMLInputElement> = useRef(null);

  return (
    <div
      className={dateInputClassNames}
      onClick={(e) => {
        e.preventDefault();
        setOpen(!open);
      }}
      {...testMetadata?.wrapper}
    >
      <ReactDatePicker
        {...otherProps} /* Deprecated: Left for backwards compatibility, use V12 */
        popperPlacement="top"
        popperModifiers={[
          {
            name: 'preventOverflow',
            options: {
              rootBoundary: 'viewport',
            },
          },
        ]}
        selected={selected}
        customInput={
          <Input
            {...inputProps}
            {...testMetadata?.input}
            inputRef={customInputRef}
          />
        }
        dateFormat={otherProps.dateFormat}
        showPopperArrow={showPopperArrow}
        {...reactDatePickerProps}
        onChange={(date: Date) => {
          onDateChange?.(date);
        }}
        onCalendarClose={() => {
          setOpen(false);
          customInputRef.current?.blur();
        }}
        onCalendarOpen={() => {
          setOpen(open);
        }}
        aria-label={labelText}
        disabled={otherProps.disabled}
        disabledKeyboardNavigation={otherProps.disabledKeyboardNavigation}
        ref={inputRef}
      />
      <Button
        iconOnly
        iconType="fa-calendar"
        labelText={labelText}
        styleType="demoted"
        sizeType="small"
        onClick={() => {
          inputRef.current?.setOpen(true);
          customInputRef.current?.focus();
        }}
        {...testMetadata?.button}
      />
    </div>
  );
};

export default DateInput;
